.popup__input {
    border: none;
    border-bottom: 1px solid rgba(0,0,0,.2);
    margin: 0 auto;
    height: 27px;
    box-sizing: border-box;
    outline: none;
    padding: 0 0 13px;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
}

