.element__button {
    background: transparent url('../../../images/like.svg') no-repeat center;
    min-width: 22px;
    min-height: 19px;
    border: 0;
    padding: 0;
    margin: 0;
    transition: 0.3s ease-in-out;
}

.element__button:hover {
    opacity: 0.5;
    cursor: pointer;
}
